export default {
  methods: {
    /**
     * 通过url下载文件
     * 适用情景：静态资源存储在第三方oss上，请求后台返回动态url的情况，根据后台返回的url下载文件
     * @param {string} url - 资源下载地址
     * @param {string} [filename] - 下载的文件名称
     * @param {boolean} [openInNewPage] - 是否在新页面打开
     */
    downloadFileByUrl(url = '', filename = 'download', openInNewPage = true) {
      let linkId = 'download-hidden-link'
      let a = document.querySelector(`#${linkId}`)
      if (!a) { // 第一次使用时新建一个 a 标签，firefox 下必须将 a 标签添加在 document.body 中，下载才能生效
        a = document.createElement("A")
        a.id = linkId
        a.style = 'display: none;'
        // rel属性旨在修复部分windows chrome69平台下，页面会跳转到下载地址的问题
        a.setAttribute('rel', 'noreferrer')
        document.body.appendChild(a)
      }
      /**
       * 对于 png, pdf, jpg 等浏览器可处理的文件格式，设置在新页面打开文件，可以防止浏览器直接在当前页面打开，阻断用户操作
       * 注意：浏览器默认会屏蔽打开新tab页／窗口的操作，需要用户手动允许
       * 关于这个问题的讨论，详见：http://jira.yuceyi.com/browse/WEB-366?focusedCommentId=11575&page=com.atlassian.jira.plugin.system.issuetabpanels%3Acomment-tabpanel#comment-11575
       */
      a.target = openInNewPage ? '_blank' : '_self'
      a.href = url
      a.download = filename
      a.click()
    },
    /**
     * 导出文件
     * 适用情景：返回接口，服务器动态生成文件，并直接返回二进制文件
     * @param {object} response - ajax返回值
     */
    exportFile(response) {
      let blob = new Blob([response.data])
      let href = window.URL.createObjectURL(blob)
      let filename = response.headers['content-disposition'] // 例如：attachment;filename="JFKEDI.xls"
      // 转译以URL形式编码的文件名
      filename = decodeURI(filename.split('"')[1])
      this.downloadFileByUrl(href, filename, false)
    },
  }
}