<template lang="pug">
transition(name='fade')
  .image-viewer(v-if='isShown')
    .carousel.slide
      button.close.text-light(type='button', @click="hideImgViewer") Close
      //- indicator
      ol.carousel-indicators
        li(v-for='(imgUrl, index) in images' :class='{ active: current === index }' @click.prevent='showByIndex(index)')
      //- Wrapper for slides
      .carousel-inner
        .carousel-item(
          v-for='(imgUrl, index) in images',
          :class='{ active: current == index}'
        )
          img.d-block.img-fluid(alt='图片', :src='imgUrl')
      //- Controls
      a.carousel-control-prev(
        :style='{ "visibility": hasPrev }'
        @click.prevent='prev'
      )
        span.carousel-control-prev-icon
      a.carousel-control-next(
        :style='{ "visibility": hasNext }'
        @click.prevent='next'
      )
        span.carousel-control-next-icon
</template>

<script>
import events from './vue-instance'

export default {
  name: 'cf-full-screen-image-viewer',
  data () {
    return {
      images: [], // 所有可预览的图片链接列表
      current: 0, // 当前展示的图片在列表中的索引
      isShown: false, // 默认不显示
    }
  },
  computed: {
    max () {
      if (this.images && this.images.length) {
        return this.images.length - 1
      } else {
        return 0
      }
    },
    hasPrev () {
      return this.current != 0 ? 'visible' : 'hidden'
    },
    hasNext () {
      return this.current != this.max ? 'visible' : 'hidden'
    },
    currentImgUrl () {
      return this.images[this.current]
    }
  },
  methods: {
    /**
     * 显示选中的index对应的图片
     */
    showByIndex (index) {
      this.current = index
    },
    prev () {
      if (this.current > 0) {
        this.current--
      } else {
        return
      }
    },
    next () {
      if (this.current < this.max) {
        this.current++
      } else {
        return
      }
    },
    /**
     * 显示图片全屏预览
     * @param {Array} option.dataSource - 图片源
     */
    showImgViewer (option) {
      this.isShown = true
      this.images = option.images
    },
    hideImgViewer () {
      this.isShown = false
      this.images = []
      // 重置当前显示图片索引
      this.current = 0
    }
  },
  mounted () {
    events.$on('show-img-viewer', this.showImgViewer)
  }

}
</script>

<style scoped>
.carousel {
  height: 100%;
}
.carousel-inner {
  padding-top: 56px;
  height: calc(100% - 56px);
}
.carousel-inner>.carousel-item {
  height: 100%;
  align-items: center;
  justify-content: center;
}
.carousel-item.active {
  display: flex;
}
.carousel-item>img {
  /*图片的宽高大于viewport时缩放，以限制图片在一定区域内显示*/
  max-height: 100%;
}
/*图片轮播全屏预览*/
.image-viewer {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0,0,0,0.7);
  z-index: 1001;
}
.image-viewer .close {
  position: absolute;
  z-index: 1001;
  opacity: 0.5;
  font-weight: normal;
}
.close {
  top: 1.25rem;
  right: 1.5rem;
}
.carousel-control-next,
.carousel-control-prev {
  cursor: pointer;
}
</style>
