import Vue from 'vue'
import Router from 'vue-router'
import Home from './components/Home'
import components from '../components'

let routerMap = [{
    path: '/',
    name: 'home',
    component: Home
}];
// 循环添加路由
Object.keys(components).forEach((file, index) => {
    routerMap.push({
        path: '/' + file,
        name: file,
        component: () => import (`../packages/${file}/example/App.vue`)
    })
})

Vue.use(Router)

export default new Router({
    routes: routerMap
})
