<template lang="pug">
nav.d-inline-block
  ol.breadcrumb
    template(v-for='(item, index) in items')
      li.breadcrumb-item.active(v-if='index === items.length - 1') {{ item.text }}
      li.breadcrumb-item(v-else)
        router-link(class='breadcrumb-item' :key='item.text' :to='item.to') {{ item.text }}
</template>

<script>
export default {
  name: 'cf-breadcrumb',
  props: {
    items: Array, // 面包屑导航 - [{显示文案,路由跳转}]
  },
}
</script>

<style lang="scss" scoped>
.breadcrumb-item a {
  color: #999;
}
.breadcrumb-item.active {
  font-weight: 500;
}
</style>