<template lang="pug">
popover(ref='popover' trigger='manual' :direction="direction" @popover-open='openEditor')
  a.btn.btn-link.text-info.p-0(href='#' :title='editBtnTitle')
    u(v-if='textValue') {{ textValue }}
    i.fa.fa-pencil(v-else)
  div(slot='content')
    .d-flex.align-items-center
      //- 提供 input 和 textarea 两种输入框
      template(v-if='type === "textarea"')
        textarea.form-control.mr-2(rows='5', v-model.trim="text")
      template(v-if='type === "inputText"')
        input.form-control.mr-2(type='text', v-model.trim="text")
      button.btn.btn-info.mr-2(type='button', @click='submit', title='确认' :disable='isRequesting')
        i.fa.fa-check
      button.btn.btn-outline-dark(type='button', @click='closeEditor', title='取消')
        i.fa.fa-times
</template>

<script>
import Popover from './Index.vue'

export default {
  name: 'popover-edit',
  props: {
    'direction': { // popover出现的位置/方向，默认显示在上方，可选：top／left／right／bottom
      type: String,
      default: 'top'
    },
    'editBtnTitle': String, // 编辑按钮上的title文案，提供可读性
    'type': { // 输入框类型：textarea／inputText
      type: String,
      default: 'inputText'
    },
    'textValue': String, // 文本框默认值
  },
  components: { Popover },
  data () {
    return {
      text: '', // 默认文本框值为空
      isRequesting: false, // 是否正在确认
    }
  },
  methods: {
    openEditor () { // 显示可编辑区域
      // 默认值是从外部传进来的
      this.text = this.textValue
    },
    closeEditor () {
      this.$refs['popover'].close()
    },
    submit () {
      this.isRequesting = true
      this.$emit('confirm', this.text, () => {
        this.isRequesting = false
        this.closeEditor()
      })
    }
  }
}
</script>
